@OptIn(InternalFlexibleApi::class)
@Composable
fun AnimatedArcProgressBar(progress: Float) {
    val arcThickness = 20.dp.toPx() //  -
    val innerColor = Color.Gray //  
    val progressColor = Color.White //  

    Canvas(modifier = Modifier.size(300.dp)) {
        //   
        drawArc(
            color = innerColor,
            startAngle = 0f, //   0 
            sweepAngle = 180f, //  180 
            useCenter = false,
            style = Stroke(width = arcThickness, cap = StrokeCap.Round) //  
        )

        //   
        drawArc(
            color = progressColor,
            startAngle = -180f, //   190 
            sweepAngle = -180f * progress, //     
            useCenter = false,
            style = Stroke(width = arcThickness, cap = StrokeCap.Round) //  
        )
    }
}


